  display: inline-block;\n    width: 100%; \n    text-align: center;\n    line-height: 24px;\n\n    a {\n      display: inline-block;\n      width: 100%; \n      padding: 1.3em;\n      text-decoration: none;\n      color: #F0EBCE;\n      position: relative;\n  \n      &:hover {\n       color: $color-text;\n       background-color: #4E6C50;\n      }\n    }\n  }\n }\n\n\n/*\n * Jumbotron\n */\n\n.hero {\n  width: 100%;\n  height: 100vh;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  background-image: linear-gradient(rgba(0,0,0, 0.5), rgba(0,0,0, 0.5)), url(\"../public/jumbotron/hero-image.jpg\");\n  color: $color-text;\n  background-size: cover;\n  background-position: 60% center;\n  background-repeat: no-repeat;\n  background-attachment: fixed;\n  text-align: center;\n\n  &__title {\n    font-weight: 500;\n    font-size: 36px;\n  }\n  \n  &__tagline {\n    margin-top: 16px;\n    font-size: 18px;\n    font-weight: 300;\n  }\n}\n\n/*\n * main\n */\n\nmain {\n  width: 100%;\n  margin: 0 auto;\n}\n\n/*\n * content\n */\n\n.content {\n  padding: 30px;\n}\n\n/*\n * latest\n */\n\n.latest {\n  width: 100%;\n  margin: 20px auto;\n\n  &__label {\n    font-size: 32px;\n    font-weight: lighter;\n    text-align: center;\n    margin-bottom: 50px;\n  }\n}\n\n/*\n * post item\n */\n\n.post-item {\n  box-shadow: 0px 4px 10px 5px  rgba(0, 0, 0, 0.2);\n  width: 100%;\n  border-radius: 5px;\n  overflow: hidden; \n  .btn-detail {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    text-decoration: none;\n    min-height: 44px;\n    min-width: 44px;\n    width: 100%;\n    padding: 8px;\n    margin-top: 16px;\n    background-color: #4E6C50;\n    color: $color-text;\n    border: none;\n    border-radius: 10px;\n\n    &:hover {\n      background-color: #395144;\n    }\n  }\n\n  .city-label {\n    color: $color-text;\n    position: relative;\n    top: -215px;\n    width: fit-content;\n    text-align: start;\n    background-color: rgba(#4E6C50, .8);\n    font-size: 12px;\n    border-bottom-right-radius: 10px;\n    padding: 10px;\n  }\n\n  &__content {\n    padding: 0 32px 32px 32px;\n  }\n\n  &__thumbnail {\n    width: 100%;\n    height: 212px;\n    object-fit: cover;\n    object-position: center;\n  }\n\n  &__food {\n    font-size: 10px;\n    color: #999;\n    margin: 5px 0 5px 0;\n  }\n\n  &__title {\n    font-weight: 500;\n    font-size: 16px;\n    transition: 0.3s opacity;\n  }\n\n  &__title a {\n    text-decoration: none;\n    color: inherit;\n  }\n\n  &__rating{\n   display: flex;\n   align-items: center;\n\n   img {\n    width: 70px;\n   }\n  }\n\n  &__description {\n    margin-top: 16px;\n    font-size: 12px;\n    text-align: justify;\n    word-spacing: -1px;\n  }\n}\n\n/*\n * Like Button\n */\n\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: #db0000;\n  \n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 0;\n  \n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n}\n\n\n/*\n * footer\n */\n\nfooter {\n  color: $color-text;\n  background-color: #395144;\n  padding: 2em;\n  width: 100%;\n  text-align: center;\n  p {\n    font-size: .8em;\n    text-align: center;\n  }\n}"],"sourceRoot":""}]);
