class AppBar extends HTMLElement {
  connectedCallback() {
    this.render();
  }

  render() {
    this.innerHTML = `
    <section id= "content" class="content">
        <div class="latest">
            <h1 class="latest__label">Explore Restaurant</h1>
            <div class="posts"></div>
        </div>
    </section>
  `;
  }
}

customElements.define('app-bar', AppBar);
